"use client";

import { useState, useEffect } from "react";
import { useRouter } from 'next/navigation';
import DashboardHeader from "@/components/dashboard-header";
import URLAnalyzer from "@/components/url-analyzer";
import ThreatDashboard from "@/components/threat-dashboard";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";

export default function DashboardPage() {
  const router = useRouter();
  const [userEmail, setUserEmail] = useState<string>("");
  const [isLoading, setIsLoading] = useState(true);
  const [activeTab, setActiveTab] = useState("overview");

  useEffect(() => {
    const checkAuth = async () => {
      try {
        const response = await fetch("/api/auth/user");
        if (!response.ok) {
          router.push("/login");
          return;
        }
        const data = await response.json();
        setUserEmail(data.email);
      } catch {
        router.push("/login");
      } finally {
        setIsLoading(false);
      }
    };

    checkAuth();
  }, [router]);

  if (isLoading) {
    return (
      <div className="min-h-screen bg-gradient-to-b from-black to-blue-900 flex items-center justify-center">
        <div className="text-center">
          <div className="w-12 h-12 rounded-full border-4 border-cyan-400/20 border-t-cyan-400 animate-spin mx-auto mb-4"></div>
          <p className="text-slate-300">Loading dashboard...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gradient-to-b from-black to-blue-900">
      <DashboardHeader email={userEmail} />
      
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-6">
        <Tabs value={activeTab} onValueChange={setActiveTab} className="w-full">
          <TabsList className="grid w-full max-w-md grid-cols-2 bg-slate-900/50 border border-slate-800">
            <TabsTrigger value="overview" className="data-[state=active]:bg-cyan-400/20 data-[state=active]:text-cyan-400">
              Threat Overview
            </TabsTrigger>
            <TabsTrigger value="analyzer" className="data-[state=active]:bg-cyan-400/20 data-[state=active]:text-cyan-400">
              IOC Analyzer
            </TabsTrigger>
          </TabsList>

          <TabsContent value="overview" className="mt-6">
            <ThreatDashboard />
          </TabsContent>

          <TabsContent value="analyzer" className="mt-6">
            <div className="bg-slate-900/30 rounded-lg p-6 border border-slate-800">
              <URLAnalyzer />
            </div>
          </TabsContent>
        </Tabs>
      </div>
    </div>
  );
}
